















































import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			userName: '',
			avatarPath: '',
			hasAvatar: false,
			isThird: 0
		};
	},
	computed: mapState(['user', 'school']), // 拿到vuex的register对象
	onLoad(parameter) {
		if (parseInt(parameter.is_third) === 1) {
			this.isThird = 1;
			this.userName = this.user.userName;
			this.avatarPath = this.user.avatar;
		}
	},
	methods: {
		//映射vuex的regSetNickName方法
		...mapMutations(['regSetUserName', 'regAfterLogin', 'regSetAvatar']),
		//点击提交按钮
		submit() {
			if (this.userName.length <= 0 || this.userName.length > 8) { //验证昵称
				uni.showToast({
					title: "请输入正确的昵称",
					icon: "none"
				});
				return;
			} else if (this.avatarPath.length === 0) { //验证头像是否上传
				uni.showToast({
					title: "您还未上传头像",
					icon: "none"
				});
				return;
			} else {
				this.regSetUserName(this.userName);
				uni.request({
					url: this.GLOBAL.serverSrc + '/common/register/register',
					method: 'POST',
					data: {
						phone: this.user.phone,
						password: this.user.password,
						user_name: this.user.userName,
						avatar: this.user.avatar,
						sex: this.user.sex,
						id: this.school.id,
						is_third: this.isThird,
						open_id: this.user.openId,
						type: this.user.type
					},
					success: res => {
						if (res.data.status === 200) { //注册成功
							this.regAfterLogin(res.data);
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							});
							uni.switchTab({
								url: '../index/index'
							});
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							});
						}
					},
					fail: (e) => {
						this.GLOBAL.requestFail(e);
					}
				});
			}
		},
		//上传头像
		uploadAvater() {
			var _this = this;
			var src = this.GLOBAL.serverSrc; //得到服务器地址
			uni.chooseImage({
				count: 1,
				success: function(chooseImageRes) {
					const tempFilePath = chooseImageRes.tempFilePaths[0];
					//开始上传头像
					uni.showLoading({
						title: '上传中...'
					});
					uni.uploadFile({
						url: src + '/common/register/uploadAvatar', //接口地址
						filePath: tempFilePath,
						name: 'avatar',
						success: (uploadFileRes) => {
							var resObj = JSON.parse(uploadFileRes.data);
							if (resObj.status === 200) {
								_this.avatarPath = src + resObj.url;
								_this.regSetAvatar(_this.avatarPath);
								_this.hasAvatar = true;
							} else {
								uni.showToast({
									title: resObj.msg,
									icon: "none"
								});
							}
						},
						fail: (e) => {
							_self.global_.requestFail(e);
						},
						complete: () => {
							uni.hideLoading();
						}
					});
				}
			});
		},
		back() {
			uni.navigateBack();
		}
	}
}
